.main .main__block {
    display: flex;
}

.block__left {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    min-width: 563px;
    justify-content: flex-end;
}

.block__left .sliders {
    display: flex;
    max-width: 563px;
}

.main__offer {
    min-width: 563px;
    overflow: hidden;
    transition: .5s ease;
}

.position__1 {
    transform: translateX(0);
}

.position__2 {
    transform: translateX(-563px);
}

.position__3 {
    transform: translateX(-1126px);
}

.main__offer h1 {
    margin-bottom: 45px;
}

.main__offer .text {
    font-size: 20px;
    line-height: 28px;
    max-width: 365px;
    text-align: right;
    margin-bottom: 52px;
}

.block__left .main__btns {
    display: flex;
    height: 68px;
    max-width: 310px;
    margin-left: 65px;
    margin-bottom: 126px;
    cursor: pointer;
}

.block__left .main__btns .first__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(110, 156, 159, 0.1);
    width: 67px;
    height: 67px;
}

.block__left .main__btns .first__btn .vector {
    width: 14px;
    height: 28px;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("./main__offer/img/Vector 1.png");
}

.block__left .main__btns:hover .first__btn {
    background-color: rgba(0, 0, 0, 0.1);
    transition: background-color .5s ease;
    cursor: pointer;
}

.block__left .main__btns a:last-child {
    max-width: 243px;
    width: 100%;
    text-decoration: none;
}

.block__left .main__btns button {
    max-width: 243px;
}

.block__left .nav__slider {
    display: flex;
    gap: 12px;
    min-width: 114px;
    max-width: 114px;
    margin: 0 auto;

}

.block__left .nav__slider button {
    width: 30px;
    height: 4px;
    border: none;
}

.block__left .nav__slider .slider__unactive {
    background-color: #D1D1D1;
    cursor: pointer;
}

.block__left .nav__slider .slider__unactive:hover {
    background-color: #8d8d8d;

}

.block__left .nav__slider .slider__active {
    background-color: black;
}

.main__block .block__right {
    width: 100%;
    position: relative;
    height: 646px;
}

.main__block .block__right .photo__1 {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 100%;
    object-fit: cover;
}

.main__block .block__right .photo__2 {
    position: absolute;
    bottom: -54px;
    left: -25px;
    width: 100%;
    max-width: 190px;
    object-fit: cover;
}

.main__block .block__right .photo__3 {
    position: absolute;
    padding-right: 15px;
    top: 123px;
    right: -106px;
    width: 100%;
    max-width: 212px;
    object-fit: cover;
}

.new__collection {
    width: 100%;
    margin-top: 190px;
}

.new__collection .goods {
    display: flex;
    justify-content: space-between;
    max-width: 100%;
    gap: 10px;
    flex-wrap: nowrap;
}

.new__collection > a {
    text-decoration: none;
}

.new__collection button {
    margin: 0 auto;
}

.block__important {
    margin-top: 130px;
    margin-bottom: 130px;
}

.block__important .block__info {
    display: flex;
    justify-content: space-between;
}

.block__important .block__info .info__item {
    max-width: 305px;
}

.block__important .block__info .info__item .item__logo {
    margin-bottom: 37px;
}

.block__important .block__info .info__item .item__desc {
    font-size: 17px;
    font-weight: 500;
    line-height: 24px;
}

.block__team .team__frame {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 487px;
}

.block__team .team__frame .desc {
    max-width: 255px;
}

.block__team .team__frame .desc h3 {
    margin-bottom: 28px;
}

.block__team .team__frame .desc .text {
    font-size: 17px;
    font-weight: 500;
    line-height: 24px;
}

.block__team .team__frame .desc .text p {
    margin-top: 28px;
    margin-bottom: 28px;
}

.block__team .team__frame .desc a {
    color: rgba(110, 156, 159, 1);
    text-decoration: none;
}

.block__team .team__frame .desc a:hover {
    border-bottom: 1px solid rgba(206, 222, 223, 1);
    transition: border-bottom-color .5s ease;
}

.block__team .team__frame .frame__gallery .gallery__slider {
    display: flex;
    position: relative;
    overflow: hidden;
    max-width: 729px;
    width: 100%;
}

.block__team .team__frame .frame__gallery .gallery__slider .position__1 {
    max-width: 100%;
    width: 100%;
    transform: translateX(0);
    transition: .5s ease;
}

.block__team .team__frame .frame__gallery .gallery__slider .position__2 {
    max-width: 100%;
    width: 100%;
    transform: translateX(-729px);
    transition: .5s ease;
}

.block__team .team__frame .frame__gallery .gallery__slider .position__3 {
    max-width: 100%;
    width: 100%;
    transform: translateX(-1458px);
    transition: .5s ease;
}

.block__team .team__frame .frame__gallery .gallery__slider .btns {
    position: absolute;
    display: flex;
    justify-content: space-between;
    max-width: 114px;
    width: 100%;
    bottom: 61px;
    left: calc(50% - 52px);
}

.block__team .team__frame .frame__gallery .gallery__slider .btns button {
    max-width: 30px;
    width: 100%;
    height: 4px;
    border: none;
}

.block__team .team__frame .frame__gallery .gallery__slider .btns .unActive {
    background-color: rgba(255, 255, 255, 0.36);
    cursor: pointer;
}

.block__team .team__frame .frame__gallery .gallery__slider .btns .unActive:hover {
    background-color: rgba(255, 255, 255, 0.7);
    transition: .5s ease;
}

.block__team .team__frame .frame__gallery .gallery__slider .btns .active {
    background-color: white;
}

.block__team .team__frame .frame__gallery {
    position: relative;
    max-width: 729px;
}

.block__team .team__frame .frame__gallery .vector {
    position: absolute;
    width: 28px;
    height: 14px;
    top: calc(50% - 7px);
    cursor: pointer;
}

.block__team .team__frame .frame__gallery .vector__left {
    background-image: url("./gallery__slider__images/Vector 1.svg");
    left: -61px;
}

.block__team .team__frame .frame__gallery .active__vector:hover {
    filter: brightness(0%);
}

.block__team .team__frame .frame__gallery .vector__right {
    background-image: url("./gallery__slider__images/Vector 2.svg");
    right: -61px;
    filter: opacity(50%);
}

.block__team .team__frame .frame__gallery .unActive__vector:hover {
    filter: opacity(100%);
    cursor: pointer;
}

@media only screen and (max-width: 1300px) {

    .main__block .block__right .photo__3 {
        display: none;
    }

    .block__team .team__frame {
        justify-content: space-around;
    }

    .block__team .team__frame .frame__gallery {
        max-width: 686px;
    }

    .main__block .block__right .photo__2 {
        left: 47px;
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__2 {
        transform: translateX(-686px);
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__3 {
        transform: translateX(calc(-2 * 686px));
    }
}

@media only screen and (max-width: 1200px) {
    .main__block .block__right .photo__2 {
        left: -85px;
        bottom: 34px;
        max-width: 160px;
    }

    .block__important .block__info .info__item {
        max-width: 295px;
    }

    .block__team .team__frame {
        justify-content: space-around;
    }

    .block__team .team__frame .frame__gallery {
        max-width: 508px;
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__2 {
        transform: translateX(-508px);
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__3 {
        transform: translateX(-1016px);
    }
}

@media only screen and (max-width: 992px) {
    .main .main__block {
        max-height: 496px;
    }

    .main__offer h1 {
        margin-bottom: 17px;
    }

    .block__left {
        justify-content: center;
        min-width: 438px;
    }

    .main__offer {
        min-width: 438px;
    }

    .main__offer .text {
        font-size: 17px;
    }

    .position__2 {
        transform: translateX(-438px);
    }

    .position__3 {
        transform: translateX(-876px);
    }

    .main__block .block__right .photo__2 {
        left: -84px;
        bottom: 10px;
        max-width: 130px;
    }

    .block__left .main__btns {
        margin-left: 35px;
        margin-bottom: 60px;
        height: 58px;
    }

    .block__left .main__btns a:last-child {
        max-width: 220px;
    }

    .block__left .main__btns .first__btn {
        height: 58px;
        width: 58px;
    }

    .main__block .block__right {
        height: 496px;
    }

    .new__collection {
        margin-top: 73px;
    }

    .block__important {
        margin-top: 93px;
        margin-bottom: 93px;
    }

    .block__important .block__info .info__item .item__logo {
        margin-bottom: 26px;
        width: 53px;
    }

    .block__important .block__info .info__item {
        max-width: 222px;
    }

    .block__important .block__info .info__item .item__desc,
    .block__team .team__frame .desc .text {
        font-size: 15px;
        line-height: 23px;
    }

    .block__team .team__frame .desc .text p {
        margin-top: 14px;
    }

    .block__team .team__frame {
        flex-direction: column;
    }

    .block__team .team__frame .desc {
        max-width: 100%;
        margin-top: 23px;
    }

    .block__team .team__frame .frame__gallery {
        max-width: 585px;
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__2 {
        transform: translateX(-585px);
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__3 {
        transform: translateX(calc(-2 * 585px));
    }
}

@media only screen and (max-width: 768px) {
    .main .main__block {
        max-height: 100%;
        flex-direction: column;
    }

    .block__left .main__btns .first__btn {
        height: 50px;
        width: 50px;
    }

    .main__block .block__right {
        display: flex;
        justify-content: center;
    }

    .main__block .block__left {
        margin-bottom: 25px;
    }

    .main__block .block__right .photo__1 {
        position: relative;
        max-height: 100%;
    }

    .main__block .block__right .photo__2 {
        left: 0;
        bottom: -25px;
        max-width: 108px;
    }

    .block__important {
        margin: 73px 0;
    }

    .block__important .block__info {
        flex-direction: column;
    }

    .block__important .block__info .info__item {
        max-width: 100%;
    }

    .block__important .block__info .info__item:not(:first-child) {
        margin-top: 25px;
    }

    .block__important .block__info .info__item h3 {
        margin-top: 7px;
    }

    .block__important .block__info .info__item .item__logo {
        position: absolute;
        right: 25px;
        width: 43px;
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__2 {
        transform: translateX(-420px);
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__3 {
        transform: translateX(calc(-2 * 420px));
    }
}

@media only screen and (max-width: 554px) {
    .block__team .team__frame .frame__gallery .vector__left,
    .block__team .team__frame .frame__gallery .vector__right {
        display: none;
    }
}

@media only screen and (max-width: 480px) {
    .block__left {
        min-width: 280px;
    }

    .main__offer {
        min-width: 300px;
    }

    .main__offer h1 {
        font-size: 32px;
        line-height: 40px;
    }

    .main__offer .text {
        text-align: left;
        font-size: 14px;
        margin-bottom: 17px;
    }

    .block__left .main__btns {
        margin-left: 0;
    }

    .new__collection .goods {
        flex-direction: column;
    }

    .new__collection .goods .good__item {
        max-width: 100%;
    }

    .block__team .team__frame .frame__gallery .gallery__slider .btns {
        bottom: 25px;
    }

    .position__2 {
        transform: translateX(-300px);
    }

    .position__3 {
        transform: translateX(calc(-2 * 300px));
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__2 {
        transform: translateX(-300px);
    }

    .block__team .team__frame .frame__gallery .gallery__slider .position__3 {
        transform: translateX(calc(-2 * 300px));
    }
}

